@value primary-dark-color from '../../theme/colors.css';
@value primary-accent-color from '../../theme/colors.css';

.input {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-right: 16px;
  font-size: 16px;
  font-family: inherit;
  outline: 0;
  padding: 4px;
  border: none;
  border-bottom: 1px solid primary-dark-color;
}

.input::-webkit-input-placeholder {
  font-weight: 400;
  color: primary-dark-color;
}

.input:-ms-input-placeholder {
  font-weight: 400;
  color: primary-dark-color;
}

.input::-ms-input-placeholder {
  font-weight: 400;
  color: primary-dark-color;
}

.input::placeholder {
  font-weight: 400;
  color: primary-dark-color;
}

.input:focus {
  border-color: primary-accent-color;
}



